import { ref, watch, type Ref } from "vue";

interface useTimezoneMode {
  timezoneMode: Ref<boolean>;
}

const loadState = (default_: boolean = true): boolean => {
  return typeof localStorage.timezoneMode === "undefined" ? default_ : localStorage.timezoneMode == "true";
};

const saveState = (): void => {
  localStorage.timezoneMode = String(timezoneMode.value);
};

const timezoneMode = ref<boolean>(loadState());

watch(timezoneMode, () => saveState());

export default function useTimezoneMode(): useTimezoneMode {
  return {
    timezoneMode,
  };
}
