import { ref, watch, type Ref } from "vue";

interface useApiPathMode {
  apiPathMode: Ref<boolean>;
}

const loadState = (default_: boolean = false): boolean => {
  return typeof localStorage.apiPathMode === "undefined" ? default_ : localStorage.apiPathMode == "true";
};

const saveState = (): void => {
  localStorage.apiPathMode = String(apiPathMode.value);
};

const apiPathMode = ref<boolean>(loadState());

watch(apiPathMode, () => saveState());

export default function useApiPathMode(): useApiPathMode {
  return {
    apiPathMode,
  };
}
